import React, { useEffect, useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { EffectCoverflow, Pagination, Navigation } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/effect-coverflow';
import 'swiper/css/pagination';
import 'swiper/css/navigation';
import './Testimonials.css';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

import marcoBeforeImage from '../assets/marco-before.png';
import marcoAfterImage from '../assets/marco-after.png';
import lauraBeforeImage from '../assets/laura-before.png';
import lauraAfterImage from '../assets/laura-after.png';
import giovanniBeforeImage from '../assets/giovanni-before.png';
import giovanniAfterImage from '../assets/giovanni-after.png';

gsap.registerPlugin(ScrollTrigger);

export const Testimonials = () => {
  const [flippedCards, setFlippedCards] = useState({});

  const testimonials = [
    {
      name: "Marco",
      text: "Credo che tu stia già facendo il massimo. Sei sempre disponibile se abbiamo bisogno (h24), sia in presenza che a distanza. Riesci a motivarci ma anche a capirci se abbiamo difficoltà.",
      beforeImage: marcoBeforeImage,
      afterImage: marcoAfterImage
    },
    {
      name: "Laura",
      text: "A parere mio è fantastico già tutta la disponibilità e la minuziosità in cui segui le persone",
      beforeImage: lauraBeforeImage,
      afterImage: lauraAfterImage
    },
    {
      name: "Giovanni",
      text: "Non credo di avere consigli, ma solo perché la mia esperienza è molto soddisfacente. Ogni volta che ho avuto bisogno hai sempre risposto rapidamente e in maniera molto chiara e esaustiva.",
      beforeImage: giovanniBeforeImage,
      afterImage: giovanniAfterImage
    },
  ];

  useEffect(() => {
    gsap.from('.testimonials h2', {
      opacity: 0,
      y: 50,
      duration: 1,
      scrollTrigger: {
        trigger: '.testimonials',
        start: 'top bottom-=100',
        toggleActions: 'play none none reverse'
      }
    });
  }, []);

  const handleCardFlip = (index, e) => {
    e.stopPropagation(); // Previene il bubbling dell'evento
    setFlippedCards(prev => ({ ...prev, [index]: !prev[index] }));
  };

  return (
<section id="testimonials" className="testimonials">
      <h2>ATLETI</h2>
      <Swiper
        effect={'coverflow'}
        grabCursor={true}
        centeredSlides={true}
        slidesPerView={'auto'}
        coverflowEffect={{
          rotate: 50,
          stretch: 0,
          depth: 100,
          modifier: 1,
          slideShadows: true,
        }}
        pagination={true}
        navigation={true}
        modules={[EffectCoverflow, Pagination, Navigation]}
        className="mySwiper"
      >
        {testimonials.map((testimonial, index) => (
          <SwiperSlide key={index}>
            <div className="testimonial-card-container">
              <div 
                className={`testimonial-card ${flippedCards[index] ? 'flipped' : ''}`} 
                onClick={(e) => handleCardFlip(index, e)}
              >
                <div className="testimonial-card-inner">
                  <div className="testimonial-card-front">
                    <div className="before-after-container">
                      <div className="image-container">
                        <img src={testimonial.beforeImage} alt={`${testimonial.name} prima`} className="transformation-image" />
                        <span>Prima</span>
                      </div>
                      <div className="image-container">
                        <img src={testimonial.afterImage} alt={`${testimonial.name} dopo`} className="transformation-image" />
                        <span>Dopo</span>
                      </div>
                    </div>
                    <p className="click-to-flip">Clicca per leggere la testimonianza</p>
                  </div>
                  <div className="testimonial-card-back">
                    <p className="testimonial-text">"{testimonial.text}"</p>
                    <p className="testimonial-name">- {testimonial.name}</p>
                  </div>
                </div>
              </div>
            </div>
          </SwiperSlide>
        ))}
      </Swiper>
    </section>
  );
};